import React, { useLayoutEffect, useCallback } from "react";
import "./Styles/Loading.css";
import lottie from "lottie-web";
import lottieLoading from "./assets/lottieLoading.json";

export default function Loading(props) {
  const loadingAnimation = useCallback(() => {
    const el = document.getElementById("laodingTxt");
    let counter = -1;
    setInterval(() => {
      counter++;
      counter === 0
        ? (el.innerHTML = "جاري التحميل ")
        : counter === 1
        ? (el.innerHTML = "جاري التحميل .")
        : counter === 2
        ? (el.innerHTML = "جاري التحميل ..")
        : counter === 3
        ? (el.innerHTML = "جاري التحميل ...")
        : (counter = -1);
    }, 400);
  }, []);

  useLayoutEffect(() => {
    loadingAnimation();
    lottie.loadAnimation({
      container: document.getElementById("loadingPageLottie"),
      renderer: "svg",
      loop: true,
      autoplay: true,
      animationData: lottieLoading,
    });
  }, [loadingAnimation]);

  return (
    <div id="loadingPage" style={{ height: window.innerHeight + "px" }}>
      <div id="loadingPageLottie"></div>
      <p id="laodingTxt">جاري التحميل</p>
      <div id="fillPageColor"></div>
    </div>
  );
}
