import React, { Fragment, Suspense, useState, useEffect, useLayoutEffect } from 'react';
import '../Styles/MainPage.css';

import { wait, introPageFadeTransition } from '../dependencies';
import { requestFrame, S } from 'selector_dom';
import { updateRenderState } from '..';
import { userData, data, tips } from '../Data';
import Diary from '../Diary';
import { useCallback } from 'react';

const SearchPage = React.lazy(() => import('../SearchPage/SearchPage'));
const DailyDetails = React.lazy(() => import('../DailyDetails'));

let [AddIsPressable, arrowIsPressable, isReversed] = [true, true, false];

export let dateIndex = 0;
export function dateGetter(i) {
  const days = ['الأحد', 'الاثنين', 'الثلاثاء', 'الاربعاء', 'الخميس', 'الجمعة', 'السبت'];
  const newDate = new Date();
  const newDecrementDate = new Date(newDate.getFullYear(), newDate.getMonth(), newDate.getDate() + i);
  const getYear = newDecrementDate.getFullYear();
  const getMonth = newDecrementDate.getMonth() + 1;
  const getDate = newDecrementDate.getDate();
  const getDay = newDecrementDate.getDay();
  return `${getYear} / ${getMonth} / ${getDate} , ${days[getDay]}`;
}

function randomNumber(min, max) {
  return Math.floor(Math.random() * (max - min + 1)) + min;
}

const tipsNum = randomNumber(0, tips.length - 1);
let isTipsOn = true;

export default function MainPage(props) {
  const [date, setDate] = useState(
    dateIndex === 0 ? 'اليوم' : dateIndex === 1 ? 'الغد' : dateIndex === -1 ? 'البارحة' : dateGetter(dateIndex)
  );

  const eatenKcalPercent = useCallback(
    () => Math.round((Math.round(NutritionSum('kcal')) / userData.userInfo.dailykcal) * 100),
    []
  );

  const setRemainPercent = useCallback(() => {
    const input = eatenKcalPercent() > 100 ? eatenKcalPercent() - 100 : eatenKcalPercent();
    S('#remainProgress').progress({
      input: input,
      strokeWidth: 6,
      strokeBackgroundColor: eatenKcalPercent() > 100 ? 'grade' : 'rgb(250,250,245,0.3)',
      textColor: eatenKcalPercent() > 100 ? '#F3D737' : 'white',
      text: eatenKcalPercent() > 100 ? '% ' + input + '+' : input + '%',
      textStyle: { fontFamily: '"Cairo", sans-serif' },
      strokeColor: eatenKcalPercent() > 100 ? '#F3D737' : 'grade',
      gradeColor1: '#FFFEFF',
      gradeColor2: '#D7FFFE',
      animation: props.doAnimation === undefined ? true : props.doAnimation,
      animationOptions: { delay: 150, duration: 1000 },
    });
  }, [eatenKcalPercent, props.doAnimation]);

  useLayoutEffect(() => {
    introPageFadeTransition('MainPage');
  }, []);

  useEffect(() => {
    setRemainPercent();
    responsiveResizing();

    window.addEventListener('resize', responsiveResizing);
    window.addEventListener('scroll', hideHeaderScrollAnimation);

    // check if tips are allowed to show
    if (!isTipsOn || window.localStorage.getItem('tips') === 'false') S('#tipsContainer').css({ display: 'none' });

    // fade intro animation

    return () => {
      window.removeEventListener('scroll', hideHeaderScrollAnimation);
      window.removeEventListener('resize', responsiveResizing);
    };
  }, [setRemainPercent, date]);

  const NutritionSum = nutrition => {
    const getDataByDate = userData.diary.filter(e => e.date === dateGetter(dateIndex))[0];
    let sum = 0;
    if (getDataByDate) {
      for (let i in getDataByDate) {
        if (i !== 'date' && i !== 'eaten') {
          const getDataById = getDataByDate[i].map(
            e => (e = Object.assign({}, data.filter(el => el.id === e[0])[0], { value: e[1], input: e[2] }))
          );

          sum += getDataById.map(e => e[nutrition] * ((e.value * e.input) / 100)).reduce((a, b) => a + b);
        }
      }
    }
    return sum;
  };

  const remainKcalSum = () => Math.round(userData.userInfo.dailykcal - NutritionSum('kcal'));

  const remainCarbSum = () => Math.round(userData.userInfo.dailyCarbs - NutritionSum('carb'));
  const carbPercent = () => Math.round((Math.round(NutritionSum('carb')) / userData.userInfo.dailyCarbs) * 100);

  const remainProteinSum = () => Math.round(userData.userInfo.dailyProtein - NutritionSum('protein'));
  const proteinPercent = () => Math.round((Math.round(NutritionSum('protein')) / userData.userInfo.dailyProtein) * 100);

  const remainFatSum = () => Math.round(userData.userInfo.dailyFat - NutritionSum('fat'));
  const fatPercent = () => Math.round((Math.round(NutritionSum('fat')) / userData.userInfo.dailyFat) * 100);

  const diarySlideAnimation = async diraction => {
    if (arrowIsPressable) {
      arrowIsPressable = false;
      const page = S('#pageBody');
      const clonedEl = page.clone(true);
      const diaryContainer = document.getElementById('diaryContainer');
      const scrollBy = S(diaryContainer).getStyle('width', true) + 1;

      if (diraction === 'toLeft') {
        diaryContainer.prepend(clonedEl);
        await wait(10);
        requestFrame(
          {
            from: diaryContainer.scrollLeft,
            to: diaryContainer.scrollLeft - scrollBy,
            duration: 300,
            easingFunction: 'easeOutQuart',
          },
          x => {
            diaryContainer.scrollTo({ left: x });
          }
        );
      } else if (diraction === 'toRight') {
        diaryContainer.append(clonedEl);
        diaryContainer.scrollTo({ left: -scrollBy });
        await wait(10);
        requestFrame(
          {
            from: diaryContainer.scrollLeft,
            to: diaryContainer.scrollLeft + scrollBy,
            duration: 300,
            easingFunction: 'easeOutQuart',
          },
          x => {
            diaryContainer.scrollTo({ left: x });
          }
        );
      }
      await wait(300);
      clonedEl.remove();
      arrowIsPressable = true;
    }
  };

  const checkDate = (decrement = false) => {
    if (arrowIsPressable) {
      decrement ? dateIndex-- : dateIndex++;
      if (dateIndex === 0) {
        setDate('اليوم');
      } else if (dateIndex === 1) {
        setDate('الغد');
      } else if (dateIndex === -1) {
        setDate('البارحة');
      } else {
        setDate(dateGetter(dateIndex));
      }
      decrement ? diarySlideAnimation('toRight') : diarySlideAnimation('toLeft');
    }
  };

  const mountSearchComponent = (title, hash) => {
    isReversed = false;
    updateRenderState(
      <Suspense fallback='' key={Math.random() * 100}>
        <SearchPage key={Math.random() * 100} title={title} doAnimation={true} />
      </Suspense>
    );
    window.history.pushState('', '', '/search#' + hash);
  };

  const hideHeaderScrollAnimation = () => {
    const getHeaderHeight = S('#header').getStyle('height', true) / 1.6;
    let multiplier = (1 / getHeaderHeight) * (getHeaderHeight - window.scrollY);
    multiplier = multiplier < 0 ? 0 : multiplier > 1 ? 1 : multiplier;
    S('.HideOnScrollElements').css({ transform: `scale(${multiplier})`, opacity: multiplier });
  };

  const responsiveResizing = () => {
    // for the header sticky postion from the top
    const getHeaderHeight = S('#header').getStyle('height', true);
    S('#header').css({ top: -getHeaderHeight + 55 + 'px' });

    // for add buttons postion from the left
    const getSideWidth = (window.innerWidth - S('#MainPage').getStyle('width', true)) / 2;
    const calcLeft = ((getSideWidth + 45) * 100) / window.innerWidth + '%';
    if (!isReversed) S('.floatButton').css({ left: calcLeft });

    // for macros percentage bars position from the bottom
    const getHeaderTitleHeight = S('#title').getStyle('height', true);
    const getHeaderPercentNumHeight = S('.percentNum').getStyle('height', true);
    const calcBottom = getHeaderTitleHeight + getHeaderPercentNumHeight + 38 + 'px';
    S('#macrosPercent').css({ bottom: calcBottom });

    // fix addbutton style if its opened while resizing
    if (window.innerWidth >= 740 && isReversed) {
      S('#addButtonPulsAnimation1, #addButtonPulsAnimation2').removeAttr('style');
      S('.floatButton').removeAttr('style');
      S('#addButton').removeAttr('style');
      S('#addButton svg').removeAttr('style');
      S('#addFoodBG').removeAttr('style');
      isReversed = false;
    }
  };

  const animateAddFoodButton = async () => {
    const getSideWidth = (window.innerWidth - S('#MainPage').getStyle('width', true)) / 2;
    const calcLeft = ((getSideWidth + 45) * 100) / window.innerWidth;
    const [breakfast, dinner, launch, snack, Bg, add, addSvg] = [
      S('#addBreakfast'),
      S('#addDinner'),
      S('#addLaunch'),
      S('#addSnacks'),
      S('#addFoodBG'),
      S('#addButton'),
      S('#addButton svg'),
    ];
    if (!isReversed && AddIsPressable && window.innerWidth < 740) {
      AddIsPressable = false;

      // hide puls animation
      S('#addButtonPulsAnimation1, #addButtonPulsAnimation2').css({ display: 'none' });
      S('.floatButton').css({ display: 'block' });
      const op = { from: [0, 6, calcLeft, 0], to: [1, 50, 50, 75], duration: 200, easingFunction: 'easeOutExpo' };

      requestFrame({ to: 1, duration: 200 }, x => {
        Bg.css({ display: 'block', opacity: x });
      });
      requestFrame({ to: 135, duration: 200 }, x => {
        addSvg.css({ transform: `rotate(${x}deg)` });
      });
      requestFrame({ from: 1, to: 1.03, duration: 200 }, x =>
        add.css({ transform: `scale(${x})`, backgroundColor: 'var(--second-color)' })
      );
      requestFrame({ ...op, delay: 50 }, (a, b, c, z) => {
        breakfast.css({ transform: `translate(-50%, -50%) scale(${a})`, bottom: `calc(${b}% + ${z}px)`, left: c + '%' });
      });
      requestFrame({ ...op, delay: 150 }, (a, b, c, z) => {
        dinner.css({ transform: `translate(-50%, -50%) scale(${a})`, bottom: b + '%', left: `calc(${c}% - ${z}px)` });
      });
      requestFrame({ ...op, delay: 250 }, (a, b, c, z) => {
        launch.css({ transform: `translate(-50%, -50%) scale(${a})`, bottom: b + '%', left: `calc(${c}% + ${z}px)` });
      });
      requestFrame({ ...op, delay: 350 }, (a, b, c, z) => {
        snack.css({ transform: `translate(-50%, -50%) scale(${a})`, bottom: `calc(${b}% - ${z}px)`, left: c + '%' });
        if (a === 1) AddIsPressable = true;
      });

      isReversed = true;
    } else if (isReversed && AddIsPressable && window.innerWidth < 740) {
      AddIsPressable = false;
      const getSideWidth = (window.innerWidth - S('#MainPage').getStyle('width', true)) / 2;
      const calcLeft = ((getSideWidth + 45) * 100) / window.innerWidth;
      const op = { from: [1, 50, 50, 75], to: [0, 6, calcLeft, 0], duration: 150, easingFunction: 'easeInCubic' };

      requestFrame({ ...op, delay: 50 }, (a, b, c, z) => {
        snack.css({ transform: `translate(-50%, -50%) scale(${a})`, bottom: `calc(${b}% - ${z}px)`, left: c + '%' });
      });
      requestFrame({ ...op, delay: 150 }, (a, b, c, z) => {
        launch.css({ transform: `translate(-50%, -50%) scale(${a})`, bottom: b + '%', left: `calc(${c}% + ${z}px)` });
      });
      requestFrame({ ...op, delay: 250 }, (a, b, c, z) => {
        dinner.css({ transform: `translate(-50%, -50%) scale(${a})`, bottom: b + '%', left: `calc(${c}% - ${z}px)` });
      });
      requestFrame({ ...op, delay: 350 }, (a, b, c, z) => {
        breakfast.css({ transform: `translate(-50%, -50%) scale(${a})`, bottom: `calc(${b}% + ${z}px)`, left: c + '%' });
      });
      requestFrame({ from: 135, to: 0, duration: 200, delay: 250 }, a => {
        addSvg.css({ transform: `rotate(${a}deg)` });
      });
      requestFrame({ from: 1.03, to: 1, duration: 200, delay: 250 }, a => {
        add.css({ transform: `scale(${a})`, backgroundColor: 'var(--main-color)' });
      });
      requestFrame({ from: 1, to: 0, duration: 200, delay: 400 + op.duration }, a => {
        Bg.css({ opacity: a });

        if (a === 0) {
          Bg.css({ display: 'none' });
          S('.floatButton').css({ display: 'none' });
          S('#addButtonPulsAnimation1, #addButtonPulsAnimation2').removeAttr('style');
          S('.floatButton').removeAttr('style');
          add.removeAttr('style');
          AddIsPressable = true;
        }
      });
      isReversed = false;
    }
  };

  const DailyDetailsHandle = () => {
    updateRenderState(
      <Suspense fallback='' key={Math.random() * 100}>
        <DailyDetails key={Math.random() * 1000} date={dateGetter(dateIndex)} />
      </Suspense>
    );
    window.history.pushState('', '', '/DailyDetailsHandle');
  };

  return (
    <Fragment>
      <div id='addFoodBG' onClick={animateAddFoodButton}></div>
      <div id='MainPage'>
        <h2 id='title'>سعراتي اليومية</h2>
        <div id='header'>
          <svg width='100%' viewBox='0 0 320 300' style={{ filter: 'drop-shadow(0px 0px 5px rgb(0, 0, 0, 0.5))' }}>
            <linearGradient id='Gradient1' gradientTransform='rotate(45)'>
              <stop offset='20%' />
              <stop offset='100%' />
            </linearGradient>
            <path d='M0 0H320V286.491C320 286.491 249 300 159 300C69 300 0 286.491 0 286.491V0Z' fill='url(#Gradient1)' />
          </svg>
          <div className='remainValues'>
            <div className='caloriesEaten'>
              <p className='HideOnScrollElements'>{Math.round(NutritionSum('kcal'))}</p>
              <p className='HideOnScrollElements'>الـمـأكـولة</p>
            </div>
            <div id='remainProgress' className='HideOnScrollElements'></div>
            <div className='caloriesRemain'>
              <p className='HideOnScrollElements' style={{ color: remainKcalSum() < 0 ? '#F3D737' : 'white' }}>
                {remainKcalSum() < 0 ? remainKcalSum() * -1 : remainKcalSum()}
              </p>
              <p className='HideOnScrollElements'>{remainKcalSum() < 0 ? 'زائــدة' : 'المتبـقية'}</p>
            </div>
          </div>
          <div id='macrosPercent'>
            <div className='macrosPercentContainer HideOnScrollElements'>
              <p className='macroName'>الكربوهيدرات</p>
              <div className='macrosPercentLine' id='macrosPercentLineCarbs' style={{ width: carbPercent() + '%' }}></div>
              <div
                className='macrosPercentLine'
                style={{
                  backgroundColor: '#F3D737',
                  backgroundImage: 'initial',
                  width: (carbPercent() > 100 ? carbPercent() - 100 : 0) + '%',
                }}
              ></div>
              <p className='percentNum'>
                <span style={{ display: remainCarbSum() < 0 ? 'none' : 'initial' }}>باقي </span>
                {remainCarbSum() < 0 ? remainCarbSum() * -1 : remainCarbSum()}
                <span>غ</span>
                <span style={{ display: remainCarbSum() < 0 ? 'initial' : 'none' }}> زائدة</span>
              </p>
            </div>
            <div className='macrosPercentContainer HideOnScrollElements'>
              <p className='macroName'>البروتين</p>
              <div className='macrosPercentLine' id='macrosPercentLineProtein' style={{ width: proteinPercent() + '%' }}></div>
              <div
                className='macrosPercentLine'
                style={{
                  backgroundColor: '#F3D737',
                  backgroundImage: 'initial',
                  width: proteinPercent() > 100 ? proteinPercent() - 100 : 0 + '%',
                }}
              ></div>
              <p className='percentNum'>
                <span style={{ display: remainProteinSum() < 0 ? 'none' : 'initial' }}>باقي </span>
                {remainProteinSum() < 0 ? remainProteinSum() * -1 : remainProteinSum()}
                <span>غ</span>
                <span style={{ display: remainProteinSum() < 0 ? 'initial' : 'none' }}> زائدة</span>
              </p>
            </div>
            <div className='macrosPercentContainer HideOnScrollElements'>
              <p className='macroName'>الدهون</p>
              <div className='macrosPercentLine' id='macrosPercentLineFat' style={{ width: fatPercent() + '%' }}></div>
              <div
                className='macrosPercentLine'
                style={{
                  backgroundColor: '#F3D737',
                  backgroundImage: 'initial',
                  width: fatPercent() > 100 ? fatPercent() - 100 : 0 + '%',
                }}
              ></div>
              <p className='percentNum'>
                <span style={{ display: remainFatSum() < 0 ? 'none' : 'initial' }}>باقي </span>
                {remainFatSum() < 0 ? remainFatSum() * -1 : remainFatSum()}
                <span>غ</span>
                <span style={{ display: remainFatSum() < 0 ? 'initial' : 'none' }}> زائدة</span>
              </p>
            </div>
          </div>
          <svg
            id='arrowDailyDetails'
            className='HideOnScrollElements'
            onClick={DailyDetailsHandle}
            xmlns='http://www.w3.org/2000/svg'
            viewBox='0 0 24 24'
          >
            <path d='M7.41 8.59L12 13.17l4.59-4.58L18 10l-6 6-6-6 1.41-1.41z' />
          </svg>
        </div>
        <div id='dateSelector'>
          <svg
            id='dateToRightArrow'
            className='dateArrow'
            xmlns='http://www.w3.org/2000/svg'
            viewBox='0 0 24 24'
            onClick={() => checkDate(true)}
          >
            <path d='M5 3l3.057-3 11.943 12-11.943 12-3.057-3 9-9z' />
          </svg>
          <p>{date}</p>
          <svg
            id='dateToLeftArrow'
            className='dateArrow'
            xmlns='http://www.w3.org/2000/svg'
            viewBox='0 0 24 24'
            transform='rotate(180)'
            style={{ transform: 'rotate(180deg)' }}
            onClick={() => checkDate(false)}
          >
            <path d='M5 3l3.057-3 11.943 12-11.943 12-3.057-3 9-9z' />
          </svg>
        </div>
        <div id='tipsContainer'>
          <svg
            xmlns='http://www.w3.org/2000/svg'
            viewBox='0 0 24 24'
            onClick={() => {
              isTipsOn = false;
              const el = S('#tipsContainer');
              const height = el.getStyle('height', true);
              requestFrame({ from: [height, 10], to: [0, 0], duration: 600, easingFunction: 'easeOutQuart' }, (h, p) => {
                el.css({ height: h + 'px', margin: p + 'px auto', paddingTop: p + 'px' });
              });
            }}
          >
            <path d='M24 20.188l-8.315-8.209 8.2-8.282-3.697-3.697-8.212 8.318-8.31-8.203-3.666 3.666 8.321 8.24-8.206 8.313 3.666 3.666 8.237-8.318 8.285 8.203z' />
          </svg>
          <p>نــصــائــح عــامــة</p>
          <p>{tips[tipsNum]}</p>
        </div>
        <div id='diaryContainer'>
          <Diary date={dateGetter(dateIndex)} />
        </div>
        <div id='addButton' onClick={animateAddFoodButton}>
          <svg viewBox='0 0 24 24'>
            <path d='M24 10h-10v-10h-4v10h-10v4h10v10h4v-10h10z' fill='white' />
          </svg>
          <div id='addButtonPulsAnimation1'></div>
          <div id='addButtonPulsAnimation2'></div>
        </div>
        <div id='addBreakfast' className='circleButton floatButton' onClick={() => mountSearchComponent('الفطور', 'breakfast')}>
          <img src={require('../assets/bread.png')} alt='bread' />
          <p>الفطور</p>
        </div>
        <div id='addLaunch' className='circleButton floatButton' onClick={() => mountSearchComponent('الغداء', 'lunch')}>
          <img src={require('../assets/soup.png')} alt='soup' />
          <p>الغداء</p>
        </div>
        <div id='addDinner' className='circleButton floatButton' onClick={() => mountSearchComponent('العشاء', 'dinner')}>
          <img src={require('../assets/fish.png')} alt='fish' />
          <p>العشاء</p>
        </div>
        <div id='addSnacks' className='circleButton floatButton' onClick={() => mountSearchComponent('وجبة خفيفة', 'snacks')}>
          <img src={require('../assets/banana.png')} alt='banana' />
          <p>وجبة خفيفة</p>
        </div>
      </div>
    </Fragment>
  );
}
