import React, { Suspense } from "react";
import "../Styles/ItemCard.css";
import { data } from "../Data";
import { updateRenderState } from "..";

import fruit from "../assets/itemsCategoriesIcons/fruit.png";
import vegetable from "../assets/itemsCategoriesIcons/vegetable.png";
import grains from "../assets/itemsCategoriesIcons/grains.png";
import fat from "../assets/itemsCategoriesIcons/fat.png";
import dairy from "../assets/itemsCategoriesIcons/dairy.png";
import bread from "../assets/itemsCategoriesIcons/bread.png";
import chicken from "../assets/itemsCategoriesIcons/chicken.png";
import paste from "../assets/itemsCategoriesIcons/paste.png";
import nut from "../assets/itemsCategoriesIcons/nut.png";
import fish from "../assets/itemsCategoriesIcons/fish.png";
import supplement from "../assets/itemsCategoriesIcons/supplement.png";
import seeds from "../assets/itemsCategoriesIcons/seeds.png";
import cake from "../assets/itemsCategoriesIcons/cake.png";
import legume from "../assets/itemsCategoriesIcons/legume.png";
import processedMeat from '../assets/itemsCategoriesIcons/sausages.png';

const ItemViewer = React.lazy(() => import("./ItemViewer"));

export default function ItemCard(props) {
  const clickHandel = () => {
    const [path, hash] = [window.location.pathname, window.location.hash];

    if (hash.length === 0 && path === "/favorites") {
      updateRenderState(
        <Suspense fallback="">
          <ItemViewer data={data.filter((e) => e.id === props.id)[0]} />
        </Suspense>
      );
      window.history.pushState("", "", `/favorites#selectMeal#${props.id}`);
    } else if (hash.length !== 0 && !hash.includes("#MealViewer") && path === "/favorites") {
      updateRenderState(
        <Suspense fallback="">
          <ItemViewer data={data.filter((e) => e.id === props.id)[0]} />
        </Suspense>
      );
      window.history.pushState("", "", "/favorites#" + hash.split("#")[1] + "#" + props.id);
    } else if (path.includes("/CreateNewMeal/search")) {
      updateRenderState(
        <Suspense fallback="">
          <ItemViewer data={data.filter((e) => e.id === props.id)[0]} />
        </Suspense>
      );
      window.history.pushState("", "", "/CreateNewMeal/search#" + props.id);
    } else if (path === "/CreateNewMeal") {
      updateRenderState(
        <Suspense fallback="">
          <ItemViewer data={data.filter((e) => e.id === props.id)[0]} value={props.value} input={props.input} />
        </Suspense>
      );
      window.history.pushState("", "", "/CreateNewMeal#" + props.id);
    } else if (path === "/CreateNewMeal/favorites") {
      updateRenderState(
        <Suspense fallback="">
          <ItemViewer data={data.filter((e) => e.id === props.id)[0]} value={props.value} input={props.input} />
        </Suspense>
      );
      window.history.pushState("", "", "/CreateNewMeal/favorites#" + props.id);
    } else if (path === "/CreateNewMeal/recentlyAdded") {
      updateRenderState(
        <Suspense fallback="">
          <ItemViewer data={data.filter((e) => e.id === props.id)[0]} value={props.value} input={props.input} />
        </Suspense>
      );
      window.history.pushState("", "", "/CreateNewMeal/recentlyAdded#" + props.id);
    } else if (path.includes("/EditMeal/search")) {
      updateRenderState(
        <Suspense fallback="">
          <ItemViewer data={data.filter((e) => e.id === props.id)[0]} />
        </Suspense>
      );
      window.history.pushState("", "", "/EditMeal/search#" + props.id);
    } else if (path === "/EditMeal") {
      updateRenderState(
        <Suspense fallback="">
          <ItemViewer data={data.filter((e) => e.id === props.id)[0]} value={props.value} input={props.input} />
        </Suspense>
      );
      window.history.pushState("", "", "/EditMeal#" + props.id);
    } else if (path === "/EditMeal/favorites") {
      updateRenderState(
        <Suspense fallback="">
          <ItemViewer data={data.filter((e) => e.id === props.id)[0]} value={props.value} input={props.input} />
        </Suspense>
      );
      window.history.pushState("", "", "/EditMeal/favorites#" + props.id);
    } else if (path === "/EditMeal/recentlyAdded") {
      updateRenderState(
        <Suspense fallback="">
          <ItemViewer data={data.filter((e) => e.id === props.id)[0]} value={props.value} input={props.input} />
        </Suspense>
      );
      window.history.pushState("", "", "/EditMeal/recentlyAdded#" + props.id);
    } else if (path.includes("search") && !hash.includes("MealViewer")) {
      updateRenderState(
        <Suspense fallback="">
          <ItemViewer data={data.filter((e) => e.id === props.id)[0]} />
        </Suspense>
      );
      window.history.pushState("", "", "/search#" + hash.split("#")[1] + "#" + props.id);
    } else if (path.includes("categoriesList")) {
      updateRenderState(
        <Suspense fallback="">
        <ItemViewer data={data.filter((e) => e.id === props.id)[0]} />
        </Suspense>
      );
      window.history.pushState("", "", path + hash + "#" + props.id);
    } else if (path === "/") {
      updateRenderState(
        <Suspense fallback="">
          <ItemViewer data={data.filter((e) => e.id === props.id)[0]} value={props.value} input={props.input} mealName={props.mealName} />
        </Suspense>
      );
      window.history.pushState("", "", "/diary#" + props.id);
    } else if (path.includes("recentlyAdded")) {
      updateRenderState(
        <Suspense fallback="">
          <ItemViewer data={data.filter((e) => e.id === props.id)[0]} value={props.value} input={props.input} />
        </Suspense>
      );
      window.history.pushState("", "", "/recentlyAdded#" + hash.split("#")[1] + "#" + props.id);
    }
  };

  const checkIcons = () => {
    if (props.description === "فاكهة") {
      return `url(${fruit})`;
    } else if (props.description === "خضار") {
      return `url(${vegetable})`;
    } else if (props.description === "حبوب") {
      return `url(${grains})`;
    } else if (props.description === "الزيت والدهون") {
      return `url(${fat})`;
    } else if (props.description === "البيض ومنتجات الحليب") {
      return `url(${dairy})`;
    } else if (props.description === "معجنات") {
      return `url(${bread})`;
    } else if (props.description === "فروج") {
      return `url(${chicken})`;
    } else if (props.description === 'الدبس والصلصات') {
      return `url(${paste})`;
    } else if (props.description === 'مكسرات') {
      return `url(${nut})`;
    } else if (props.description === 'أسماك') {
      return `url(${fish})`;
    } else if (props.description === 'مكملات غذائية') {
      return `url(${supplement})`;
    } else if (props.description === 'بذور') {
      return `url(${seeds})`;
    } else if (props.description === 'البسكوت والحلويات') {
      return `url(${cake})`;
    } else if (props.description === 'لحوم مصنعة') {
      return `url(${processedMeat})`;
    } else if (props.description === 'بقوليات') {
      return `url(${legume})`;
    }
  };

  return (
    <div className="searchItemsContainer" onClick={clickHandel}>
      <div className="searchItemsCategoriesIcon" style={{ backgroundImage: checkIcons() }}></div>
      <div>
        <p className="searchItemsName">{props.name}</p>
        <p className="searchItemsCategories">{props.brand}</p>
        <p className="searchItemsCategories">{props.description}</p>
        <p className="searchItemsInfo">
          {props.kcal} سعرة حرارية
          <span> • </span>
          {props.serving}
          <svg
            className="searchItemsVerified"
            viewBox="0 0 128 128"
            xmlns="http://www.w3.org/2000/svg"
            style={{ display: !props.id.includes("addedByUsers") ? "inline-block" : "none" }}
          >
            <g>
              <circle cx="64" cy="64" fill="#4ce797" r="43.125" />
              <circle cx="64" cy="64" fill="var(--background-kinda-white)" r="34.544" />
              <path
                d="m58.211 81.479a3.894 3.894 0 0 1 -2.694-1.079l-11.569-11.1a3.892 3.892 0 1 1 5.388-5.618l8.59 8.239 20.468-24.03a3.893 3.893 0 1 1 5.927 5.048l-23.147 27.171a3.893 3.893 0 0 1 -2.767 1.364c-.065.003-.13.005-.196.005z"
                fill="#4ce797"
              />
            </g>
          </svg>
          <svg
            className="searchItemsVerified"
            style={{ display: props.id.includes("addedByUsers") ? "inline-block" : "none" }}
            enableBackground="new 0 0 24 24"
            viewBox="0 0 24 24"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="m17.25 24c-3.722 0-6.75-3.028-6.75-6.75s3.028-6.75 6.75-6.75 6.75 3.028 6.75 6.75-3.028 6.75-6.75 6.75z"
              fill="#2196f3"
            />
            <path d="m17.25 21c-.552 0-1-.448-1-1v-5.5c0-.552.448-1 1-1s1 .448 1 1v5.5c0 .552-.448 1-1 1z" fill="#fff" />
            <path d="m20 18.25h-5.5c-.552 0-1-.448-1-1s.448-1 1-1h5.5c.552 0 1 .448 1 1s-.448 1-1 1z" fill="#fff" />
            <path
              d="m23.98 3.66v.04c0 .97-.38 1.9-1.07 2.59l-1.94 1.98-5.25-5.22 1.92-1.96c.69-.7 1.62-1.1 2.61-1.11.98.03 1.92.38 2.63 1.08.7.69 1.1 1.61 1.1 2.6z"
              fill="#f44336"
            />
            <path
              d="m5.15 18.79-2.59-2.59c-.06.07-.11.14-.14.23l-2.38 6.56c-.09.28-.03.58.18.79.14.14.33.22.53.22.09 0 .17-.01.26-.04l6.56-2.38c.09-.03.16-.08.23-.14z"
              fill="#ffe082"
            />
            <path d="m8.5 17.25c0 1.04.18 2.04.52 2.97l-1.22 1.22-2.65-2.65 3.61-3.65c-.17.67-.26 1.38-.26 2.11z" fill="#ffb300" />
            <path d="m20.97 8.27-.75.75c-.93-.34-1.93-.52-2.97-.52-.75 0-1.48.1-2.18.28l3.18-3.21 2.71 2.69z" fill="#ffb300" />
            <path d="m18.25 5.57-3.18 3.21c-3.1.8-5.54 3.25-6.31 6.36l-3.61 3.65-2.59-2.59.08-.08 12.53-12.52.55-.55z" fill="#ffc107" />
            <path
              d="m17.25 10.5c-3.723 0-6.75 3.027-6.75 6.75s3.027 6.75 6.75 6.75v-3c-.553 0-1-.447-1-1v-1.75h-1.75c-.553 0-1-.447-1-1s.447-1 1-1h1.75v-1.75c0-.553.447-1 1-1z"
              fill="#1d83d4"
            />
            <path d="m17.25 18.25h-1v1.75c0 .553.447 1 1 1zm0-4.75c-.553 0-1 .447-1 1v1.75h1z" fill="#dedede" />
            <path d="m17.25 16.25h-1-1.75c-.553 0-1 .447-1 1s.447 1 1 1h1.75 1z" fill="#dedede" />
            <path d="m20.97 8.27-.75.75c-.93-.34-1.93-.52-2.97-.52-.75 0-1.48.1-2.18.28l3.18-3.21 2.71 2.69z" fill="#ffb300" />
            <path
              d="m22.91 1.09-4.568 4.568 2.628 2.612 1.94-1.979c.69-.69 1.07-1.62 1.07-2.59v-.041c0-.976-.388-1.883-1.07-2.57z"
              fill="#d43a2f"
            />
            <path d="m5.18 18.82-4.96 4.96c.14.14.33.22.53.22l.26-.04 6.56-2.38c.09-.03.16-.08.23-.14z" fill="#dec371" />
            <path
              d="m8.727 15.273-3.547 3.547 2.62 2.62 1.22-1.221c-.34-.93-.52-1.93-.52-2.97 0-.681.078-1.346.227-1.976z"
              fill="#de9c00"
            />
          </svg>
        </p>
      </div>
    </div>
  );
}
