import React, { Suspense } from "react";
import "../Styles/MealCard.css";
import { updateRenderState } from "..";

const MealViewer = React.lazy(() => import("./MealViewer"));

export default function MealCard(props) {
  const mountMealViewer = () => {
    const [path, hash] = [window.location.pathname, window.location.hash];

    if (path === "/favorites" && hash === "#Meals") {
      window.history.pushState("", "", "/favorites#Meals#MealViewer");
    } else if (path === "/favorites" && hash.length !== 0) {
      window.history.pushState("", "", "/favorites#Meals#" + hash.split("#")[2] + "#MealViewer");
    } else if (path === "/search" && hash.length !== 0) {
      window.history.pushState("", "", "/search#Meals#" + hash.split("#")[1] + "#MealViewer");
    }

    updateRenderState(
      <Suspense fallback="" key={Math.random() * 100}>
        <MealViewer key={Math.random() * 1000} doAnimation={true} name={props.name} kcal={props.kcal} />
      </Suspense>
    );
  };

  return (
    <div className="mealCardContainer" onClick={mountMealViewer}>
      <div className="mealCardIcon"></div>
      <div className="mealCardInfoContainer">
        <p className="mealCardName">{props.name}</p>
        <p className="mealCardIngredients">
          {props.ingredients === 1 ? "مكون واحد" : props.ingredients === 2 ? "مكونان" : props.ingredients}
          {props.ingredients === 1 || props.ingredients === 2 ? "" : " مكونات"}
        </p>
        <p className="mealCardKcal">{Math.round(props.kcal)} سعرة حرارية</p>
      </div>
    </div>
  );
}
