import React, { Suspense, useLayoutEffect } from "react";
import "./Styles/Diary.css";
import { updateRenderState } from ".";
import { userData, data, storeData } from "./Data";
import { S } from "selector_dom"
import html2canvas from "html2canvas";

import MainPage from "./NavicationBar/MainPage";
import NavicationBar from "./NavicationBar/NavicationBar";
import ItemCard from "./Foods/ItemCard";

const SearchPage = React.lazy(() => import("./SearchPage/SearchPage"));
const CreateMeal = React.lazy(() => import("./Meals/CreateMeal"));
const MealDetails = React.lazy(() => import("./Meals/MealDetails"));

export default function Diary(props) {
  useLayoutEffect(() => {
    checkEatenMeal("breakfast");
    checkEatenMeal("lunch");
    checkEatenMeal("dinner");
    checkEatenMeal("snacks");
  });

  const checkEatenMeal = (m) => {
    const getData = userData.diary.filter((e) => e.date === props.date)[0];
    if (getData && getData.eaten && getData.eaten.includes(m)) {
      S("#mealEaten-" + m).css({ backgroundColor: "var(--main-color)", border: "none" });
      S("#mealEaten-" + m + " svg").css({ fill: "white" });
    } else if (!getData || !getData.eaten || !getData.eaten.includes(m)) {
      S("#mealEaten-" + m).removeAttr("style");
      S("#mealEaten-" + m + " svg").removeAttr("style");
    }
  };

  const eatenMeal = (m) => {
    const getData = userData.diary.filter((e) => e.date === props.date)[0];
    if (!getData.eaten) getData.eaten = [];
    if (getData.eaten.includes(m)) {
      S("#mealEaten-" + m).removeAttr("style");
      S("#mealEaten-" + m + " svg").removeAttr("style");
      getData.eaten.splice(getData.eaten.indexOf(m), 1);
      if (getData.eaten.length === 0) delete getData.eaten;
    } else {
      S("#mealEaten-" + m).css({ backgroundColor: "var(--main-color)", border: "none" });
      S("#mealEaten-" + m + " svg").css({ fill: "white" });
      getData.eaten.push(m);
    }
    storeData();
  };

  const mountSearchComponent = (ar, en) => {
    updateRenderState(
      <Suspense fallback="" key={Math.random() * 100}>
        <SearchPage key={Math.random() * 100} title={ar} doAnimation={true} />
      </Suspense>
    );
    window.history.pushState("", "", "/search#" + en);
  };

  const mealKcalSum = (mealName) => {
    const getDataByDate = userData.diary.filter((e) => e.date === props.date)[0];
    if (getDataByDate && getDataByDate[mealName]) {
      const getDataById = getDataByDate[mealName].map(
        (e) => (e = Object.assign({}, data.filter((el) => el.id === e[0])[0], { value: e[1], input: e[2] }))
      );
      return Math.round(getDataById.map((e) => e.kcal * ((e.value * e.input) / 100)).reduce((a, b) => a + b));
    }
  };

  const renderMeal = (mealName) => {
    const getDataByDate = userData.diary.filter((e) => e.date === props.date)[0];
    if (getDataByDate && getDataByDate[mealName]) {
      const getDataById = getDataByDate[mealName].map(
        (e) => (e = Object.assign({}, data.filter((el) => el.id === e[0])[0], { value: e[1], input: e[2] }))
      );
      return getDataById.map((e) => (
        <ItemCard
          key={e.id + Math.random() * 1000}
          name={e.name}
          kcal={Math.round((e.kcal / 100) * (e.value * e.input))}
          serving={e.value === 1 ? e.input + " غرام " : e.input + "  " + e.serving.filter((el) => el[1] === e.value)[0][0]}
          id={e.id}
          brand={e.brand}
          description={e.description}
          value={e.value}
          input={e.input}
          mealName={mealName}
        />
      ));
    }
  };

  const mealDotsHandle = (m) => {
    const popId = `#${m}PopUp`;
    const isScale1 = S(popId).getStyle("transform").includes("1");
    S(".mealPopUp").css({ transform: "scale(0)" });
    if (isScale1) {
      S(popId).css({ transform: "scale(0)" });
    } else {
      S(popId).css({ transform: "scale(1)" });
      document.addEventListener(
        "click",
        (e) => {
          if (
            e.target.className !== "newMealPopUp" &&
            e.target.id !== "breakfastPopUpSvg" &&
            e.target.id !== "lunchPopUpSvg" &&
            e.target.id !== "dinnerPopUpSvg" &&
            e.target.id !== "snacksPopUpSvg"
          ) {
            S(".mealPopUp").css({ transform: "scale(0)" });
          }
        },
        { once: true }
      );
    }
  };

  const createMeal = (mealName) => {
    const getData = [...userData.diary.filter((e) => e.date === props.date)[0][mealName]];
    userData.favorite.tmpMeal.mealContent = getData;
    userData.favorite.tmpMeal.mealName = "";
    updateRenderState(
      <Suspense fallback="" key={Math.random() * 100}>
        <CreateMeal key={Math.random() * 100} doAnimation={true} goBackOnce={true} />
      </Suspense>
    );
    window.history.pushState("", "", "/CreateNewMeal");
  };

  const deleteAll = (m) => {
    const getDataByDate = userData.diary.filter((e) => e.date === props.date)[0];
    delete getDataByDate[m];
    updateRenderState([<MainPage key={Math.random() * 100} doAnimation={false} />, <NavicationBar key={Math.random() * 100} />]);
    storeData();
  };

  const screenShotShare = (el) => {
    window.scrollTo({ top: 0 });
    S(".mealPopUp").css({ transform: "scale(0)" });
    S(".mealName").css({ boxShadow: "none" });
    S("body").css({ letterSpacing: "0px" });
    html2canvas(document.querySelector("#" + el), {
      onclone: () => {
        S(".mealName").removeAttr("style");
        S("body").removeAttr("style");
      },
    }).then((canvas) => {
      canvas.toBlob((blob) => {
        var file = new File([blob], "share.png", { type: "image/png", lastModified: Date.now() });

        if (navigator.canShare && navigator.canShare({ files: [file] })) {
          navigator
            .share({
              files: [file],
              title: document.querySelector("title").innerHTML,
              text: window.location.origin,
            })
            .then(() => console.log("Share was successful."))
            .catch((error) => console.log("Sharing failed", JSON.stringify(error)));
        } else {
          console.log(`Your system doesn't support sharing files.`);
        }
      });
    });
  };

  return (
    <div id="pageBody">
      <div className="mealFoods" id="breakfastContainer">
        <div className="mealName">
          <h3>الفطور</h3>
          <svg
            style={{ display: renderMeal("breakfast") ? "block" : "none" }}
            onClick={() => mealDotsHandle("breakfast")}
            id="breakfastPopUpSvg"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path d="M12 18c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3zm0-9c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3zm0-9c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3z" />
          </svg>
          <div className="mealPopUp" id="breakfastPopUp">
            <p onClick={() => createMeal("breakfast")} className="newMealPopUp">
              إنشاء وجبة
            </p>
            <p
              className="newMealPopUp"
              onClick={() => {
                updateRenderState(
                  <Suspense fallback="" key={Math.random() * 100}>
                    <MealDetails key={Math.random() * 100} mealName="breakfast" date={props.date} />
                  </Suspense>
                );
                window.history.pushState("", "", "/MealDetails");
              }}
            >
              تفاصيل الوجبة
            </p>
            <p className="newMealPopUp" onClick={() => deleteAll("breakfast")}>
              حذف المحتويات
            </p>
            <p
              className="newMealPopUp"
              style={{ display: navigator.canShare ? "block" : "none" }}
              onClick={() => screenShotShare("breakfastContainer")}
            >
              مشاركة الوجبة
            </p>
          </div>
        </div>
        <div
          className="mealFoodsImgP"
          onClick={() => mountSearchComponent("الفطور", "breakfast")}
          style={{ display: renderMeal("breakfast") ? "none" : "block" }}
        >
          <img src={require("./assets/bread.png")} alt="bread" />
          <p className="addYourFoodText">أضف طعامك</p>
        </div>
        {renderMeal("breakfast")}
        <div className="mealKcalSum" style={{ display: renderMeal("breakfast") ? "flex" : "none" }}>
          <p>{mealKcalSum("breakfast")} سعرة حرارية</p>
          <div
            id="mealEaten-breakfast"
            className="mealEaten"
            data-html2canvas-ignore="true"
            onClick={() => eatenMeal("breakfast")}
          >
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
              <path d="M21 6.285l-11.16 12.733-6.84-6.018 1.319-1.49 5.341 4.686 9.865-11.196 1.475 1.285z" />
            </svg>
          </div>
        </div>
      </div>
      <div className="mealFoods" id="lunchContainer">
        <div className="mealName">
          <h3>الغداء</h3>
          <svg
            style={{ display: renderMeal("lunch") ? "block" : "none" }}
            onClick={() => mealDotsHandle("lunch")}
            id="lunchPopUpSvg"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path d="M12 18c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3zm0-9c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3zm0-9c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3z" />
          </svg>
          <div className="mealPopUp" id="lunchPopUp">
            <p onClick={() => createMeal("lunch")} className="newMealPopUp">
              إنشاء وجبة
            </p>
            <p
              className="newMealPopUp"
              onClick={() => {
                updateRenderState(
                  <Suspense fallback="" key={Math.random() * 100}>
                    <MealDetails key={Math.random() * 100} mealName="lunch" date={props.date} />
                  </Suspense>
                );
                window.history.pushState("", "", "/MealDetails");
              }}
            >
              تفاصيل الوجبة
            </p>
            <p className="newMealPopUp" onClick={() => deleteAll("lunch")}>
              حذف المحتويات
            </p>
            <p
              className="newMealPopUp"
              style={{ display: navigator.canShare ? "block" : "none" }}
              onClick={() => screenShotShare("lunchContainer")}
            >
              مشاركة الوجبة
            </p>
          </div>
        </div>
        <div
          className="mealFoodsImgP"
          onClick={() => mountSearchComponent("الغداء", "lunch")}
          style={{ display: renderMeal("lunch") ? "none" : "block" }}
        >
          <img src={require("./assets/soup.png")} alt="soup" />
          <p className="addYourFoodText">أضف طعامك</p>
        </div>
        {renderMeal("lunch")}
        <div className="mealKcalSum" style={{ display: renderMeal("lunch") ? "flex" : "none" }}>
          <p>{mealKcalSum("lunch")} سعرة حرارية</p>
          <div id="mealEaten-lunch" className="mealEaten" data-html2canvas-ignore="true" onClick={() => eatenMeal("lunch")}>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
              <path d="M21 6.285l-11.16 12.733-6.84-6.018 1.319-1.49 5.341 4.686 9.865-11.196 1.475 1.285z" />
            </svg>
          </div>
        </div>
      </div>
      <div className="mealFoods" id="dinnerContainer">
        <div className="mealName">
          <h3>العشاء</h3>
          <svg
            style={{ display: renderMeal("dinner") ? "block" : "none" }}
            onClick={() => mealDotsHandle("dinner")}
            id="dinnerPopUpSvg"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path d="M12 18c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3zm0-9c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3zm0-9c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3z" />
          </svg>
          <div className="mealPopUp" id="dinnerPopUp">
            <p onClick={() => createMeal("dinner")} className="newMealPopUp">
              إنشاء وجبة
            </p>
            <p
              className="newMealPopUp"
              onClick={() => {
                updateRenderState(
                  <Suspense fallback="" key={Math.random() * 100}>
                    <MealDetails key={Math.random() * 100} mealName="dinner" date={props.date} />
                  </Suspense>
                );
                window.history.pushState("", "", "/MealDetails");
              }}
            >
              تفاصيل الوجبة
            </p>
            <p className="newMealPopUp" onClick={() => deleteAll("dinner")}>
              حذف المحتويات
            </p>
            <p
              className="newMealPopUp"
              style={{ display: navigator.canShare ? "block" : "none" }}
              onClick={() => screenShotShare("dinnerContainer")}
            >
              مشاركة الوجبة
            </p>
          </div>
        </div>
        <div
          className="mealFoodsImgP"
          onClick={() => mountSearchComponent("العشاء", "dinner")}
          style={{ display: renderMeal("dinner") ? "none" : "block" }}
        >
          <img src={require("./assets/fish.png")} alt="fish" />
          <p className="addYourFoodText">أضف طعامك</p>
        </div>
        {renderMeal("dinner")}
        <div className="mealKcalSum" style={{ display: renderMeal("dinner") ? "flex" : "none" }}>
          <p>{mealKcalSum("dinner")} سعرة حرارية</p>
          <div id="mealEaten-dinner" className="mealEaten" data-html2canvas-ignore="true" onClick={() => eatenMeal("dinner")}>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
              <path d="M21 6.285l-11.16 12.733-6.84-6.018 1.319-1.49 5.341 4.686 9.865-11.196 1.475 1.285z" />
            </svg>
          </div>
        </div>
      </div>
      <div className="mealFoods" id="snacksContainer">
        <div className="mealName">
          <h3>وجبة خفيفة</h3>
          <svg
            style={{ display: renderMeal("snacks") ? "block" : "none" }}
            onClick={() => mealDotsHandle("snacks")}
            id="snacksPopUpSvg"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path d="M12 18c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3zm0-9c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3zm0-9c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3z" />
          </svg>
          <div className="mealPopUp" id="snacksPopUp">
            <p onClick={() => createMeal("snacks")} className="newMealPopUp">
              إنشاء وجبة
            </p>
            <p
              className="newMealPopUp"
              onClick={() => {
                updateRenderState(
                  <Suspense fallback="" key={Math.random() * 100}>
                    <MealDetails key={Math.random() * 100} mealName="snacks" date={props.date} />
                  </Suspense>
                );
                window.history.pushState("", "", "/MealDetails");
              }}
            >
              تفاصيل الوجبة
            </p>
            <p className="newMealPopUp" onClick={() => deleteAll("snacks")}>
              حذف المحتويات
            </p>
            <p
              className="newMealPopUp"
              style={{ display: navigator.canShare ? "block" : "none" }}
              onClick={() => screenShotShare("snacksContainer")}
            >
              مشاركة الوجبة
            </p>
          </div>
        </div>
        <div
          className="mealFoodsImgP"
          onClick={() => mountSearchComponent("وجبة خفيفة", "snacks")}
          style={{ display: renderMeal("snacks") ? "none" : "block" }}
        >
          <img src={require("./assets/banana.png")} alt="banana" />
          <p className="addYourFoodText">أضف طعامك</p>
        </div>
        {renderMeal("snacks")}
        <div className="mealKcalSum" style={{ display: renderMeal("snacks") ? "flex" : "none" }}>
          <p>{mealKcalSum("snacks")} سعرة حرارية</p>
          <div id="mealEaten-snacks" className="mealEaten" data-html2canvas-ignore="true" onClick={() => eatenMeal("snacks")}>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
              <path d="M21 6.285l-11.16 12.733-6.84-6.018 1.319-1.49 5.341 4.686 9.865-11.196 1.475 1.285z" />
            </svg>
          </div>
        </div>
      </div>
    </div>
  );
}
